/* App.css */
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* New Styles for Tabs */
.tabs {
  display: flex;
  justify-content: center;
  margin: 20px;
}

.tabs button {
  background-color: #61dafb;
  color: white;
  border: none;
  padding: 20px 40px; /* Increased padding */
  margin: 0 10px;
  cursor: pointer;
  font-size: 18px;
  font-weight: bold;
  border-radius: 8px;
  transition: background-color 0.3s ease, transform 0.3s ease;
}

.tabs button:hover {
  background-color: #21a1f1;
  transform: scale(1.1);
}

.tabs button:focus {
  outline: none;
  box-shadow: 0 0 0 3px rgba(97, 218, 251, 0.5);
}

.tab-content {
  margin: 20px;
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 10px;
  width: 80%;
  max-width: 600px;
  text-align: left;
  background-color: white;
  color: black;
}
